import React, { useState, useEffect } from "react";

const Scroll = () => {
  const [scrollActive, setScrollActive] = useState("active");

  const scrollTop = () => {
    window.scrollTo(0, 0);
  };
  function easeScroll() {
    let scrolTotal = window.scrollY;
    let docHeight = document.body.scrollHeight;
    let scrollPercent = scrolTotal / docHeight;

    if (scrollPercent > 0.55) {
      setScrollActive((pre) => (pre = "active"));
    } else {
      setScrollActive((pre) => (pre = ""));
    }
  }
  useEffect(() => {
    window.addEventListener("scroll", easeScroll);
    return () => window.removeEventListener("scroll", easeScroll);
  }, [scrollActive]);
  return (
    <div
      className={`scroll-box ${scrollActive}`}
      onClick={scrollTop}
    >
      <svg
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M12 16C12.2833 16 12.521 15.904 12.713 15.712C12.9043 15.5207 13 15.2833 13 15V11.8L13.9 12.7C14.0833 12.8833 14.3167 12.975 14.6 12.975C14.8833 12.975 15.1167 12.8833 15.3 12.7C15.4833 12.5167 15.575 12.2833 15.575 12C15.575 11.7167 15.4833 11.4833 15.3 11.3L12.7 8.7C12.6 8.6 12.4917 8.529 12.375 8.487C12.2583 8.44567 12.1333 8.425 12 8.425C11.8667 8.425 11.7417 8.44567 11.625 8.487C11.5083 8.529 11.4 8.6 11.3 8.7L8.7 11.3C8.51667 11.4833 8.425 11.7167 8.425 12C8.425 12.2833 8.51667 12.5167 8.7 12.7C8.88333 12.8833 9.11667 12.975 9.4 12.975C9.68333 12.975 9.91667 12.8833 10.1 12.7L11 11.8V15C11 15.2833 11.096 15.5207 11.288 15.712C11.4793 15.904 11.7167 16 12 16ZM12 22C10.6167 22 9.31667 21.7373 8.1 21.212C6.88333 20.6873 5.825 19.975 4.925 19.075C4.025 18.175 3.31267 17.1167 2.788 15.9C2.26267 14.6833 2 13.3833 2 12C2 10.6167 2.26267 9.31667 2.788 8.1C3.31267 6.88333 4.025 5.825 4.925 4.925C5.825 4.025 6.88333 3.31233 8.1 2.787C9.31667 2.26233 10.6167 2 12 2C13.3833 2 14.6833 2.26233 15.9 2.787C17.1167 3.31233 18.175 4.025 19.075 4.925C19.975 5.825 20.6873 6.88333 21.212 8.1C21.7373 9.31667 22 10.6167 22 12C22 13.3833 21.7373 14.6833 21.212 15.9C20.6873 17.1167 19.975 18.175 19.075 19.075C18.175 19.975 17.1167 20.6873 15.9 21.212C14.6833 21.7373 13.3833 22 12 22ZM12 20C14.2167 20 16.1043 19.221 17.663 17.663C19.221 16.1043 20 14.2167 20 12C20 9.78333 19.221 7.89567 17.663 6.337C16.1043 4.779 14.2167 4 12 4C9.78333 4 7.896 4.779 6.338 6.337C4.77933 7.89567 4 9.78333 4 12C4 14.2167 4.77933 16.1043 6.338 17.663C7.896 19.221 9.78333 20 12 20Z"
          fill="currentColor"
        />
      </svg>
      <span>TOP</span>
    </div>
  );
};

export default Scroll;
